.wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000 url(../img/bg_products.jpg) no-repeat 0 0;
  background-size: 100%;
}
.wrap .wrap-1000 {
  width: 1000px;
  margin: 0 auto;
}
.wrap .header {
  padding-top: 90px;
  margin-bottom: 60px;
  overflow: hidden;
}
.wrap .header .logo {
  float: left;
  display: block;
}
.wrap .header .readme {
  float: right;
  line-height: 58px;
  font-size: 20px;
  color: #00518b;
  font-weight: bold;
  text-decoration: none;
}
.wrap .header .readme:active {
  color: #444;
}
.wrap .content .pro-list {
  margin: 0 -40px;
}
.wrap .content .pro-list li {
  float: left;
  width: 260px;
  margin: 0 40px 40px;
}
.wrap .content .pro-list li .pro-item {
  position: relative;
  border: 1px solid #000;
}
.wrap .content .pro-list li .pro-item table {
  width: 100%;
  background: #fff;
}
.wrap .content .pro-list li .pro-item table td {
  padding: 5px 10px;
  border: 1px solid #000;
}
.wrap .content .pro-list li .pro-item table td.profile {
  width: 80px;
}
.wrap .content .pro-list li .pro-item .pro-item-buy {
  position: absolute;
  right: -40px;
  top: -5px;
  width: 80px;
  height: 80px;
  line-height: 80px;
  border-radius: 50%;
  color: #0070b8;
  text-align: center;
  text-decoration: none;
  border: 1px solid #000;
  font-size: 36px;
}
.wrap .content .pro-list li .pro-item .pro-item-buy span,
.wrap .content .pro-list li .pro-item .pro-item-buy i {
  display: block;
  border-radius: 50%;
  font-style: normal;
}
.wrap .content .pro-list li .pro-item .pro-item-buy span {
  width: 70px;
  height: 70px;
  line-height: 70px;
  border: 5px solid #f4b1b3;
}
.wrap .content .pro-list li .pro-item .pro-item-buy i {
  width: 68px;
  height: 68px;
  line-height: 68px;
  border: 1px solid #000;
  background: #fff;
}
.wrap .content .pro-list li .pro-item .pro-item-buy:active i {
  background: #ccc;
}
.wrap .bottom {
  text-align: right;
}
.wrap .bottom a {
  font-size: 30px;
  color: #0070b8;
  text-decoration: none;
  text-shadow: rgba(255, 255, 255, 0.8) 1px 0 0, rgba(255, 255, 255, 0.8) 0 1px 0, rgba(255, 255, 255, 0.8) -1px 0 0, rgba(255, 255, 255, 0.8) 0 -1px 0;
}
.wrap .footer {
  position: fixed;
  left: 0;
  bottom: 30px;
  width: 100%;
  text-align: center;
  line-height: 30px;
  color: #000;
  font-weight: bold;
  letter-spacing: 3px;
  text-shadow: rgba(255, 255, 255, 0.8) 1px 0 0, rgba(255, 255, 255, 0.8) 0 1px 0, rgba(255, 255, 255, 0.8) -1px 0 0, rgba(255, 255, 255, 0.8) 0 -1px 0;
}
